<template>
  <div>
    <div id="back_div">

      <div>
        <img src="../../assets/imgs/01.png"
             style="width:100px;height:100px">
      </div>
      <div class="sons2">
        <h3>总订单</h3>
        <p>{{totalOrder}}</p>
      </div>

      <div>
        <img src="../../assets/imgs/02.png"
             style="width:100px;height:100px">
      </div>
      <div class="sons2">
        <h3>总销售额</h3>
        <p>{{totalAmount}}</p>
      </div>

      <div>
        <img src="../../assets/imgs/03.png"
             style="width:100px;height:100px"> </div>
      <div class="sons2">
        <h3>今日订单数</h3>
        <p>{{todayOrder}}</p>
      </div>

      <div>
        <img src="../../assets/imgs/04.png"
             style="width:100px;height:100px"> </div>
      <div class="sons2">
        <h3>今日销售额</h3>
        <p>{{totayAmount}}</p>
      </div>
    </div>
    <!-- <ve-line :data="chartData"
             style="margin-top:50px"></ve-line> -->
    <div id="MYcharts"></div>
  </div>

</template>

<script>
import { totaldata } from '@/API/apis'
import echarts from 'echarts'
export default {
  mounted () {
    var MYcharts = echarts.init(document.getElementById("MYcharts"))

    totaldata().then(res => {
      let option = {
        title: {
          text: "数据统计"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["金额数据", "订单数据"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: res.data.xData //横轴
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "金额数据",
            type: "line",
            stack: "总量",
            data: res.data.amountData
          },
          {
            name: "订单数据",
            type: "line",
            stack: "总量",
            data: res.data.orderData
          }
        ]
      };
      MYcharts.setOption(option);
    });
  },
  created () {
    totaldata().then(res => {
      this.totalOrder = res.data.totalOrder
      this.totalAmount = res.data.totalAmount
      this.todayOrder = res.data.todayOrder
      this.totayAmount = res.data.totayAmount
    })
  },
  data () {
    return {
      totalOrder: 0,
      totalAmount: 0,
      todayOrder: 0,
      totayAmount: 0,
    }
  }
}
</script>

<style lang="less" scoped>
#back_div {
  display: flex;
  justify-content: space-around;
  .sons2 {
    padding-top: 20px;
    line-height: 30px;
  }
  img {
    width: 100px;
    height: 100px;
    vertical-align: middle;
  }
  h3 {
    text-align: center;
    color: #cccccc;
  }
  p {
    text-align: center;
  }
}
#MYcharts {
  width: 100%;
  height: 500px;
}
</style>